@use '../base/reset';
@use '../abstracts';
@use '../layout/grid';

html {
    box-sizing: border-box;
}

body {
    background-color: abstracts.$xdark;
    min-width: 350px;
    overflow-x: hidden;

    @include abstracts.tablet {
        background-image: url('../assets/tablet/image-host.jpg');
        background-position: top right;
        background-repeat: no-repeat;
    }

    @include abstracts.mobile {
        background-image: url('../assets/mobile/image-host.jpg');
        // background-position: top right;
        background-size: cover;
        
    }
}

.background__overlay {
    @include abstracts.mobile {
        height: 100%;
        width: 100vw;
        position: fixed;
        background-color: rgba(18, 23, 37, 0.8);
        z-index: -1;
    }
}

.logo {
    grid-row: 1 / 4;
    grid-column: 1 / 3;
    align-items: center;

    @include abstracts.tablet {
        grid-row: 1 / 3;
    }

    @include abstracts.mobile {
        margin: 0 auto;
    }

    & img {
        height: 56px;
        width: auto;
    }
}

.hero {
    &__image--host {
        grid-column: 5 / -1;
        grid-row: 3 / 12;
        transform: translateY(-12px);
        z-index: 1;

        @include abstracts.tablet {
            display: none;
        }
        @include abstracts.mobile {
        }
    };

    &__image--vector {
        grid-column: -2 / -1;
        grid-row: 11 / 12;
        z-index: 2;

        @include abstracts.tablet {
            grid-column: 1 / 5;
            grid-row: 14 / 15;
        }
        @include abstracts.mobile {
            display: none;
        }
    }

    &__cta {
        grid-column: 1 / auto;
        grid-row: 5 / 12;
        z-index: 3;
        position: relative;

        @include abstracts.tablet {
            grid-column: 1 / -2;
        }
        @include abstracts.mobile {
            background-color: transparent;
        };
        
        &--container {
            background-color: abstracts.$xdark;
            padding-top: 72px;
            padding-right: 72px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            width: 665px;
            height: 421px;
            position: absolute;
            bottom: 0px;
            left: 0px;

            @include abstracts.mobile {
                width: 100%;
                position: relative;
                justify-content: space-between;
                background-color: transparent;
            }

        }

        &--copy {
            width: 445px;
            height: 84px;
            margin-bottom: 20px;

            @include abstracts.mobile {
                max-width: 50ch;
                min-width: 20ch;
                order: 1;
                margin: 0 auto;
            }
        }

        &--icons {
            width: 536px;
            height: 29px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            @include abstracts.mobile {
                width: 100%;
                order: 2;
            }

            & > img:first-child, img:nth-child(2) {
                height: 29px;
                width: auto;

                @include abstracts.mobile {
                    height: 17px;
                    width: auto;
                }
            }

            & > img:nth-child(3) {
                height: 18px;
                width: auto;

                @include abstracts.mobile {
                    height: 11px;
                    width: auto;
                }
            }

            & > img:last-child {
                height: 26px;
                width: auto;

                @include abstracts.mobile {
                    height: 15px;
                    width: auto;
                }
            }
        }

        &--form {
            display: flex;
            flex-direction: column;
            @include abstracts.mobile {
                align-items: center;
                order: 3;
            }
        }
    }
}
