@use './abstracts';

.grid {
    display: grid;
    grid-template-columns: repeat(12, minmax(auto, 65px));
    grid-column-gap: 30px;
    grid-auto-rows: 60px;
    grid-row-gap: 12px;
    margin: 0 auto;
    width: 75%;

    @include abstracts.tablet {
        grid-template-columns: repeat(12, 48px);
        grid-column-gap: 10px;
        width: 90vw;
    }

    @include abstracts.mobile {
        display: flex;
        flex-direction: column;
        justify-content:space-evenly;
    }

}

.grid__align--center {
    display: grid;
    align-items: center;

    @include abstracts.mobile {
        // display: block;
        padding: 56px 0px 36px 0px;
    }

}
