@use '../abstracts';

button {
    width: 162px;
    height: 46px;
    border-radius: 28px;
    border: none;
    background-color: abstracts.$green;
    margin-left: -172px;

    @include abstracts.mobile{
        width: 326px;
        margin: 15px auto;
    }

    &:active {
        outline: 0;
    }

    &:focus, &:hover {
        background-color: abstracts.$greenActive;
        outline: 0;
    }
}