@use '../abstracts';

input {
    height: 56px;
    width: 427px;
    border-radius: 28px;
    border: none;
    background-color: abstracts.$dark;

    @include abstracts.mobile {
        width: 296px;
        height: 46px;
    }

    &:focus{
        background-color: abstracts.$dark;
        outline: 0;
    }
}

#signup {
    @include abstracts.mobile {
        display: flex;
        flex-direction: column;
    }

}