@use '../abstracts';

$font : 'Chivo', sans-serif;
$light: 300;
$bold: 700;

body {
    color: abstracts.$white;
}
 
#title {
    font-family: $font;
    font-weight: $light;
    font-size: 52px;
    line-height: 62px;
    text-transform: uppercase;

    @include abstracts.tablet{
        font-size: 48px;
    }

    @include abstracts.mobile{
        font-size: 26px;
        line-height: 38px;
        text-align: center;
    }
}

p {
    font-family: $font;
    font-weight: $light;
    color: abstracts.$vlight;
    font-size: 18px;
    line-height: 2;

    // @include abstracts.tablet{
    //     line-height: 1.75;
    // }
    @include abstracts.mobile{
        font-size: 15px;
        text-align: center;
        line-height: 2;
    }
}

.green {
    display: block;
    color: abstracts.$green;
}

input {
    font-family: $font;
    font-weight: $bold;
    color: abstracts.$white;
    font-size: 14px;
    padding-left: 30px;
}

button {
    font-family: $font;
    font-weight: $bold;
    color: abstracts.$black;
    font-size: 14px;
    text-align: center;
}

#formMessage {
    font-family: $font;
    font-weight: $bold;
    font-size: 12px;
    padding-left: 30px;

    @include abstracts.mobile {
    }
}

.error {
    color: abstracts.$error;
}

.success {
    color: abstracts.$green;
}

.hidden {
    display: none;
}